// Config Management Component
// Allows users to save, load, export, import, and share configurations

'use client';

import { useState, useRef } from 'react';
import { Download, Upload, Share2, Save, Clock } from 'lucide-react';
import { motion, AnimatePresence } from 'framer-motion';
import type { DistroId } from '@/lib/data';
import {
    createConfig,
    exportConfig,
    importConfig,
    generateShareUrl,
    copyToClipboard,
    saveToRecentConfigs,
    getRecentConfigs,
    type AppConfig,
} from '@/lib/configManager';

interface ConfigManagerProps {
    selectedDistro: DistroId;
    selectedApps: Set<string>;
    onLoadConfig: (distro: DistroId, apps: string[]) => void;
}

export function ConfigManager({ selectedDistro, selectedApps, onLoadConfig }: ConfigManagerProps) {
    const [isOpen, setIsOpen] = useState(false);
    const [recentConfigs, setRecentConfigs] = useState<AppConfig[]>([]);
    const [showCopySuccess, setShowCopySuccess] = useState(false);
    const fileInputRef = useRef<HTMLInputElement>(null);

    // Load recent configs when dropdown opens
    const handleOpen = () => {
        setIsOpen(!isOpen);
        if (!isOpen) {
            setRecentConfigs(getRecentConfigs());
        }
    };

    // Export current config
    const handleExport = () => {
        const config = createConfig(selectedDistro, selectedApps);
        exportConfig(config);
        saveToRecentConfigs(config);
        setIsOpen(false);
    };

    // Import config from file
    const handleImport = () => {
        fileInputRef.current?.click();
    };

    const handleFileChange = async (e: React.ChangeEvent<HTMLInputElement>) => {
        const file = e.target.files?.[0];
        if (!file) return;

        try {
            const config = await importConfig(file);
            onLoadConfig(config.distro, config.apps);
            saveToRecentConfigs(config);
            setIsOpen(false);
        } catch (err) {
            alert(err instanceof Error ? err.message : '导入失败');
        }

        // Reset input
        e.target.value = '';
    };

    // Save current config to recent
    const handleSave = () => {
        const name = prompt('为此配置命名（可选）：');
        if (name === null) return; // User cancelled

        const config = createConfig(selectedDistro, selectedApps, name || undefined);
        saveToRecentConfigs(config);
        setRecentConfigs(getRecentConfigs());
    };

    // Share current config
    const handleShare = async () => {
        const config = createConfig(selectedDistro, selectedApps);
        const url = generateShareUrl(config);
        const success = await copyToClipboard(url);

        if (success) {
            setShowCopySuccess(true);
            setTimeout(() => setShowCopySuccess(false), 2000);
        } else {
            alert('复制失败，请手动复制：\n' + url);
        }
    };

    // Load a recent config
    const handleLoadRecent = (config: AppConfig) => {
        onLoadConfig(config.distro, config.apps);
        setIsOpen(false);
    };

    if (selectedApps.size === 0) return null;

    return (
        <div className="relative">
            {/* Main Button */}
            <button
                onClick={handleOpen}
                className="flex items-center gap-2 px-3 py-1.5 rounded-lg bg-[var(--card-bg)] border border-[var(--border-primary)] hover:border-[var(--accent)] transition-all duration-300 text-sm"
                style={{ transition: 'all 0.3s' }}
            >
                <Save className="w-4 h-4" />
                <span className="hidden sm:inline">配置</span>
            </button>

            {/* Dropdown Menu */}
            <AnimatePresence>
                {isOpen && (
                    <>
                        {/* Backdrop */}
                        <motion.div
                            initial={{ opacity: 0 }}
                            animate={{ opacity: 1 }}
                            exit={{ opacity: 0 }}
                            className="fixed inset-0 z-40"
                            onClick={() => setIsOpen(false)}
                        />

                        {/* Menu */}
                        <motion.div
                            initial={{ opacity: 0, y: -10, scale: 0.95 }}
                            animate={{ opacity: 1, y: 0, scale: 1 }}
                            exit={{ opacity: 0, y: -10, scale: 0.95 }}
                            transition={{ duration: 0.2 }}
                            className="absolute right-0 mt-2 w-72 bg-[var(--card-bg)] border border-[var(--border-primary)] rounded-xl shadow-2xl overflow-hidden z-50"
                        >
                            {/* Actions */}
                            <div className="p-3 space-y-1">
                                <button
                                    onClick={handleSave}
                                    className="w-full flex items-center gap-3 px-3 py-2 rounded-lg hover:bg-[var(--hover-bg)] transition-colors text-left"
                                >
                                    <Save className="w-4 h-4 text-[var(--accent)]" />
                                    <div className="flex-1">
                                        <div className="text-sm font-medium">保存配置</div>
                                        <div className="text-xs text-[var(--text-muted)]">保存到本地历史</div>
                                    </div>
                                </button>

                                <button
                                    onClick={handleExport}
                                    className="w-full flex items-center gap-3 px-3 py-2 rounded-lg hover:bg-[var(--hover-bg)] transition-colors text-left"
                                >
                                    <Download className="w-4 h-4 text-green-500" />
                                    <div className="flex-1">
                                        <div className="text-sm font-medium">导出为文件</div>
                                        <div className="text-xs text-[var(--text-muted)]">下载 JSON 配置</div>
                                    </div>
                                </button>

                                <button
                                    onClick={handleImport}
                                    className="w-full flex items-center gap-3 px-3 py-2 rounded-lg hover:bg-[var(--hover-bg)] transition-colors text-left"
                                >
                                    <Upload className="w-4 h-4 text-blue-500" />
                                    <div className="flex-1">
                                        <div className="text-sm font-medium">导入配置</div>
                                        <div className="text-xs text-[var(--text-muted)]">从文件加载</div>
                                    </div>
                                </button>

                                <button
                                    onClick={handleShare}
                                    className="w-full flex items-center gap-3 px-3 py-2 rounded-lg hover:bg-[var(--hover-bg)] transition-colors text-left relative"
                                >
                                    <Share2 className="w-4 h-4 text-purple-500" />
                                    <div className="flex-1">
                                        <div className="text-sm font-medium">分享链接</div>
                                        <div className="text-xs text-[var(--text-muted)]">复制分享 URL</div>
                                    </div>
                                    {showCopySuccess && (
                                        <motion.span
                                            initial={{ opacity: 0, scale: 0.8 }}
                                            animate={{ opacity: 1, scale: 1 }}
                                            exit={{ opacity: 0 }}
                                            className="absolute right-2 text-xs text-green-500 font-medium"
                                        >
                                            已复制!
                                        </motion.span>
                                    )}
                                </button>
                            </div>

                            {/* Recent Configs */}
                            {recentConfigs.length > 0 && (
                                <>
                                    <div className="border-t border-[var(--border-primary)] px-3 py-2">
                                        <div className="flex items-center gap-2 text-xs text-[var(--text-muted)] font-medium">
                                            <Clock className="w-3 h-3" />
                                            最近保存
                                        </div>
                                    </div>
                                    <div className="px-3 pb-3 space-y-1 max-h-60 overflow-y-auto">
                                        {recentConfigs.map((config, idx) => (
                                            <button
                                                key={config.timestamp}
                                                onClick={() => handleLoadRecent(config)}
                                                className="w-full px-3 py-2 rounded-lg hover:bg-[var(--hover-bg)] transition-colors text-left"
                                            >
                                                <div className="text-sm font-medium truncate">
                                                    {config.name || `配置 ${idx + 1}`}
                                                </div>
                                                <div className="flex items-center gap-2 text-xs text-[var(--text-muted)] mt-0.5">
                                                    <span className="uppercase">{config.distro}</span>
                                                    <span>·</span>
                                                    <span>{config.apps.length} 个应用</span>
                                                    <span>·</span>
                                                    <span>{new Date(config.timestamp).toLocaleDateString('zh-CN')}</span>
                                                </div>
                                            </button>
                                        ))}
                                    </div>
                                </>
                            )}
                        </motion.div>
                    </>
                )}
            </AnimatePresence>

            {/* Hidden file input */}
            <input
                ref={fileInputRef}
                type="file"
                accept=".json"
                onChange={handleFileChange}
                className="hidden"
            />
        </div>
    );
}
